import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";
import { GlobalConstants } from "src/app/global_constant";
import { throwError } from "rxjs";

export class User {
  company_name: String;
  aadhar: String;
  gst: String;
  pan: String;
  address: String;
  name: String;
  username: String;
  email: String;
  contact_number: String;
}

@Component({
  selector: "app-accountsetting",
  templateUrl: "./accountsetting.component.html",
  styleUrls: ["./accountsetting.component.scss"],
})
export class AccountsettingComponent implements OnInit {
  accountForm: FormGroup;
  updatePwd: FormGroup;
  user: User;
  user_id: any;
  url = GlobalConstants.url;
  msg = null;
  err = null;
  loader = true;
  totalcount = 2;
  error = null;

  //test

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public jwtService: JwtService,
    private activtedrouter: ActivatedRoute
  ) {}

  ngOnInit(): void {
    //get user_id
    this.user_id = localStorage.getItem("user_id");
    console.log("accountsetting", this.user_id);
    //get userprofile
    this.jwtService.profile().subscribe(
      (res) => {
        this.user = res;
        console.log(this.user);
        this.accountForm = this.fb.group({
          company_name: [res.company_name],
          aadhar: [res.aadhar],
          gst: [res.gst],
          pan: [res.pan],
          address: [res.address],
          name: [res.name],
          username: [res.username],
          id: [res.id],
          email: [res.email],
          contact_number: [res.contact_number],
        });
      },
      (error) => {
        console.log(error);
        if (error.status === 401) {
          alert(error.error.message);
          this.jwtService.logout();
        }
      }
    );
    //updateaccountinfo_form
    this.accountForm = this.fb.group({
      id: [this.user_id],
      name: [],
      email: [],
      username: [],
      contact_number: [],
      company_name: [],
      address: [],
      gst: [],
      pan: [],
      aadhar: [],
    });
    //update password
    this.updatePwd = this.fb.group({
      id: [this.user_id],
      password: [""],
      password_confirmation: [""],
    });
  }

  showAccount: boolean = false;
  //update profile
  onSubmit() {
    console.log(this.accountForm.value);

    this.jwtService.updateProfile(this.accountForm.value).subscribe(
      (res) => {
        this.ngOnInit(), (this.msg = res);
      },
      (error) => {
        this.err = error.error.message;
      }
    );
  }

  updatePassword() {
    console.log(this.updatePwd.value);
    this.jwtService.updatePassword(this.updatePwd.value).subscribe(
      (result) => {
        alert("Password updated successfully");
        console.log(this.updatePwd.value);
        this.updatePwd.reset({ id: this.user_id });
        // this.router.navigate(['accountsetting']);
      },
      (error) => {
        this.handleError(error);
      }
    );
  }

  handleError(error) {
    let errorMsg = "";
    if (error.error instanceof ErrorEvent) {
      errorMsg = `Error: ${error.error.message}`;
    } else {
      errorMsg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(errorMsg);
  }
}

