import { Component, OnInit, ViewChild } from "@angular/core";
import { Location } from "@angular/common";
import { map, find } from "rxjs/operators";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { NgxCsvParser } from "ngx-csv-parser";
import { ngxCsv } from "ngx-csv/ngx-csv";
import { NgxCSVParserError } from "ngx-csv-parser";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { SmsServiceService } from "src/app/shared/sms-service.service";

@Component({
  selector: "app-import-con",
  templateUrl: "./import-con.component.html",
  styleUrls: ["./import-con.component.css"],
})
export class ImportConComponent implements OnInit {
  import_template: FormGroup;
  user_id = "";
  msg = null;
  err = null;
  mahesh = null;
  columnofcsv = [];
  cSV: any[] = [];
  header = false;
  showUploadSection: boolean;
  constructor(
    public location: Location,
    public fb: FormBuilder,
    private ngxCsvParser: NgxCsvParser,
    public smsService: SmsServiceService
  ) {}

  col = [
    { id: "template_id", value: "Template Id" },
    { id: "content", value: "Content" },
  ];

  @ViewChild("fileImportInput") fileImportInput: any;

  fileChangeListener($event: any): void {
    const files = $event.srcElement.files;
    console.log(files);
    if (files) {
      this.showUploadSection = true;
      this.arr1 = [];
      this.array = {};
      this.final_aray1 = [];
      this.final_aray2 = [];
    }

    const header_row = 0;

    this.ngxCsvParser
      .parse(files[0], { header: this.header, delimiter: "," })
      .pipe()
      .subscribe(
        (result: Array<any>) => {
          console.log("Result", result);
          this.cSV = result;

          console.log(this.cSV[2].length);

          this.columnofcsv = this.mahesh = [];
          console.log(this.mahesh);

          for (var i = 0; i < this.cSV[header_row].length; i++) {
            var name_row = this.cSV[header_row][i];
            var num = this.mahesh.push(name_row);
          }
        },
        (error: NgxCSVParserError) => {
          console.log("Error", error);
        }
      );
  }

  //selectedcolumn with respect to csvcolumn
  arr1 = [];
  array = {};
  final_aray1 = [];
  final_aray2 = [];
  selectedCol(e: any, selectedcol) {
    var field_id = selectedcol;
    var field_value = e.target.value;
    // var id = "id";

    if (!this.arr1.includes(field_value)) {
      this.arr1.push(field_value);
      // console.log(selectedcol, "field_id");
      // console.log(e.target.value, "field_val");
      if (this.array[selectedcol] !== field_value) {
        for (var i = 1; i < this.cSV.length; i++) {
          if (field_id == "template_id") {
            var array1 = {};
            (array1["no"] = i),
              (array1["id"] = this.user_id),
              (array1[field_id] = this.cSV[i][field_value]);

            this.final_aray1.push(array1);
          }
        }
        for (var i = 1; i < this.cSV.length; i++) {
          if (field_id == "content") {
            var array2 = {};
            (array2["no"] = i),
              (array2["id"] = this.user_id),
              (array2[field_id] = this.cSV[i][field_value]);
            this.final_aray2.push(array2);
          }
        }
      }
    } else {
      alert("Duplicate Column Not Allowed Please select other");
      e.target.value = "";
    }
    // console.log(this.array);
  }

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("new template import", this.user_id);

    //show table
    this.showUploadSection = false;

    //import template through csv
    this.import_template = this.fb.group({
      // id: [this.user_id],
      template_id: [],
      content: [],
    });
  }

  uploadTemplate() {
    let finalUpload = this.final_aray1.map((no) => {
      let otherNo = this.final_aray2.find((element) => element.no === no.no);
      return { ...no, ...otherNo };
    });
    console.log("final", finalUpload);

    var data = [{ id: this.user_id }, finalUpload];
    console.log("data", data);

    // if (finalUpload[0].template_id !== "" || finalUpload[0].content !== "") {
    //   console.log(finalUpload[0].template_id);
    //   console.log(finalUpload[0].content);

    this.smsService.templateImport(data).subscribe(
      (res) => {
        console.log(res);
        if (res.status == 200) {
          this.ngOnInit(), (this.msg = res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: res.message,
            showConfirmButton: false,
            timer: 1000,
          });
        }
        if (res.status == 403) {
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: res.message,
            showConfirmButton: false,
            timer: 1000,
          });
        }
      },
      (error) => {
        this.err = error.error;
      }
    );
    // } else {
    //   alert("please select both column");
    // }
  }
}
