import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class ResellerService {
  url = GlobalConstants.url;
  // user_id = GlobalConstants.user_id;

  constructor(private http: HttpClient) {}

  //create user by reseller//done
  public createUserByRes(data) {
    return this.http.post<any>(this.url + "adduser", data);
  }
  //get reseller user  list  //done
  public getResellerUserList() {
    return this.http.get<any>(this.url + "getusers");
  }
  //get single user  //done
  public getUser(reseller_user_id: any) {
    return this.http.get<any>(this.url + "getuser/" + reseller_user_id);
  }
  //fund transfer to user
  public fundTransfer(data) {
    return this.http.post<any>(this.url + "fundtransfer", data);
  }
  //transaction log which trn by user to below user
  public transacLog(reseller_user_id) {
    return this.http.get<any>(this.url + "transactionlog/" + reseller_user_id);
  }
  //dlr summary
  public dlrSummary(reseller_user_id) {
    return this.http.get<any>(this.url + "dlrSummary/" + reseller_user_id);
  }
  //low balance alert set
  lowBalanceAlert() {}

  //changeAccountType demo
  public chnageType(data) {
    return this.http.post<any>(this.url + "changeDemoAcc", data);
  }
  //setExpiry
  setExpiry(data) {
    return this.http.post<any>(this.url + "setExpiry", data);
  }

  /////////Domain Whitelabel///////
  public getWhiteLabelData(data) {
    return this.http.get<any>(this.url + "getLabelData/" + data);
  }

  //add domain
  public addDom(data) {
    return this.http.post<any>(this.url + "addDomain", data);
  }
  public getlabel() {
    return this.http.get<any>(this.url + "getLabel");
  }
  public getSingleLabel(id) {
    return this.http.get<any>(this.url + "getSingleLabel/" + id);
  }
  //delete
  public deletelabel(id) {
    return this.http.get<any>(this.url + "deletLabel/" + id);
  }

  public addHeader(data) {
    return this.http.post<any>(this.url + "addHeader", data);
  }
  public addContactUs(data) {
    return this.http.post<any>(this.url + "addFooter", data);
  }
  public addSocialLink(data) {
    return this.http.post<any>(this.url + "addSocial_link", data);
  }
  //addAbout

  public addAbout(data) {
    return this.http.post<any>(this.url + "addAbout", data);
  }

  public addService(data) {
    return this.http.post<any>(this.url + "addService", data);
  }

  public addSeoMeta(data) {
    return this.http.post<any>(this.url + "addSEO", data);
  }
  public addOther(data) {
    return this.http.post<any>(this.url + "addOther", data);
  }

  //
  // upload(file: File): Observable<Object> {
  //   const formData: FormData = new FormData();
  //   formData.append("avatar", file, file.name);

  //   return this.http.post(this.url + `upload`, formData);
  // }
}
