import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { JwtService } from "./../../shared/jwt.service";
import { TokenAuthService } from "../../shared/token-auth.service";
import { AuthenticationStateService } from "../../shared/authentication-state.service";
import { LoginInAuthService } from "src/app/login-in-auth.service";
import Swal from "sweetalert2/dist/sweetalert2.js";

export class UserId {
  id: any;
  name: any;
}
@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.scss"],
})
export class LoginComponent implements OnInit {
  // signinForm: FormGroup;
  err = null;
  mahesh_id = "";
  user_details: UserId;

  constructor(
    public router: Router,
    public fb: FormBuilder,
    public fb2: FormBuilder,
    public jwtService: JwtService,
    private tokenAuthService: TokenAuthService,
    private authenticationStateService: AuthenticationStateService,
    private auth: LoginInAuthService
  ) {
    // redirect to home if already logged in
    if (this.tokenAuthService.getJwtToken) {
      this.router.navigate(["/services"]);
    }
  }

  /////new form check for validation
  signForm = new FormGroup({
    email: new FormControl("", [Validators.required, Validators.email]),
    password: new FormControl("", [
      Validators.required,
      Validators.minLength(6),
    ]),
  });
  //hfgh
  get email() {
    return this.signForm.get("email");
  }
  get password() {
    return this.signForm.get("password");
  }

  ngOnInit() {
    if (this.auth.LoggedIn) {
      this.router.navigate(["/services"]);
    }
  }

  onSubmit() {
    this.jwtService.logIn(this.signForm.value).subscribe(
      (res) => {
        // sessionStorage.setItem('user_id', this.userId);
        console.log(res.user.id);
        this.user_details = res.user;
        this.mahesh_id = res.user.id;

        this.tokenStorage(res);
        this.userDetails(res);

        Swal.fire({
          position: "center",
          icon: "success",
          title: "Successfully LoggedIn!",
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        console.log(error.error);

        this.err = error.error;
        // console.log(error);
        // Swal.fire({
        //   position: "top-center",
        //   icon: "error",
        //   title: this.err.error,
        //   showConfirmButton: false,
        //   timer: 1000,
        // });
      },
      () => {
        this.authenticationStateService.setAuthState(true);
        this.signForm.reset();
        this.router.navigate(["services"]);
        // this.reloadPage();
      }
    );
  }

  tokenStorage(jwt) {
    this.tokenAuthService.setTokenStorage(jwt.access_token);
  }
  reloadPage(): void {
    window.location.reload();
  }
  //set user details in service for futureuse
  userDetails(user) {
    this.jwtService.setUserDetails(user);
  }
}
