import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { AuthenticationStateService } from "src/app/shared/authentication-state.service";
import { TokenAuthService } from "src/app/shared/token-auth.service";
import { Router } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import { SmsServiceService } from "src/app/shared/sms-service.service";
import { PhonebookService } from "src/app/shared/phonebook/phonebook.service";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";

import { ActivatedRoute } from "@angular/router";

// export class selectPhone{
//   phonebook_id:any;
//   phonebook_name:any;
// }
export class User {
  name: String;
  email: String;
  username: String;
}
export class Data {
  phonebook_name: String;
  phonebook_id: String;
}

@Component({
  selector: "app-header-new",
  templateUrl: "./header-new.component.html",
  styleUrls: ["./header-new.component.css"],
})
export class HeaderNewComponent implements OnInit {
  user: User;
  msg = null;
  err = null;
  url = GlobalConstants.url;
  user_id: any;
  searchedKeyword: string;
  username: string;

  constructor(
    public authenticationStateService: AuthenticationStateService,
    private tokenAuthService: TokenAuthService,
    public router: Router,
    public jwtService: JwtService,
    private modalService: NgbModal,
    public fb: FormBuilder,
    public smsService: SmsServiceService,
    public phonebookService: PhonebookService,
    private http: HttpClient,
    private activatedRoute: ActivatedRoute
  ) {}

  ngOnInit(): void {
    this.username = localStorage.getItem("username");

    //get user profile
    this.jwtService.profile().subscribe((res: any) => {
      this.user = res;
    });
  }

  logOut() {
    // this.authenticationStateService.setAuthState(false);
    // this.tokenAuthService.destroyToken();
    // this.router.navigate(["signin"]);
    this.jwtService.logout();
  }
}
