import { Component, OnInit, ViewChild } from "@angular/core";
import { Location } from "@angular/common";
import { VirtualserviceService } from "src/app/shared/virtualservice-service/virtualservice.service";
import { FormBuilder, FormGroup } from "@angular/forms";
import { DaterangepickerDirective } from "ngx-daterangepicker-material";
import * as moment from "moment";
import { ngxCsv } from "ngx-csv/ngx-csv";

@Component({
  selector: "app-reportvirtual",
  templateUrl: "./reportvirtual.component.html",
  styleUrls: ["./reportvirtual.component.css"],
})
export class ReportvirtualComponent implements OnInit {
  virtual_report: [];
  virtual_number_list: [];
  keyword_details: [];
  maxDate: any;
  searchedKeyword: string;
  //pagination
  p: number = 1;
  count: number = 10;
  selected: any = [
    moment().format("DD-MM-YYYY"),
    moment().format("DD-MM-YYYY"),
  ];

  @ViewChild(DaterangepickerDirective, { static: false })
  pickerDirective: DaterangepickerDirective;

  constructor(
    public location: Location,
    public virtualService: VirtualserviceService,
    public fb: FormBuilder
  ) {}

  ngOnInit(): void {
    this.virtualService.getVirtualNoList().subscribe(
      (res: any) => {
        console.log("no", res);
        this.virtual_number_list = res.data;
      },
      (error) => {
        console.log(error);
        // if (error.status === 401) {
        //   alert(error.error.message);
        //   this.jwtService.logout();
        // }
      }
    );

    //get keywords
    this.virtualService.getKeyword().subscribe(
      (res: any) => {
        console.log("keyword", res);
        this.keyword_details = res.data;
      },
      (error) => {
        console.log(error);
      }
    );
    //get report
    this.virtualService.getInboxReport().subscribe(
      (res: any) => {
        console.log("inbox", res);
        this.virtual_report = res.data;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  change(val: any) {
    this.maxDate = moment(val);
    var start = moment(val.startDate._d).format("DD-MM-YYYY");
    var end = moment(val.endDate._d).format("DD-MM-YYYY");
    console.log(start);
    console.log(end);
    this.selected = [start, end];
  }

  searchReportVirtual(val: any) {
    console.log(val);

    this.virtualService.searchEnquires(val).subscribe(
      (res: any) => {
        console.log("filter", res);
        if ((res.status = 201)) {
          this.virtual_report = res.data;
        } else if ((res.status = 202)) {
          alert("No data Found");
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }
  clearFormValue(searchForm) {
    searchForm.reset({
      keywords: "",
      number: "",
      date: [moment().format("DD/MM/YYYY"), moment().format("DD/MM/YYYY")],
      sender: "",
    });
    this.ngOnInit();
  }

  exportReport(val: any) {
    console.log(val);

    this.virtualService.searchEnquires(val).subscribe(
      (res: any) => {
        console.log(res);
        if (res.status == 201) {
          let i = 0;
          console.log(res.data);

          let csvFileName = "virtualreport" + moment().format("YYYYMMDDSSMMHH");
          let finalData = res.data.map(function (obj) {
            return {
              No: (i = i + 1),
              Number: obj.missed_call_number,
              Caller: obj.caller,
              Circle: obj.circle != null ? obj.circle : "NA",
              Operator: obj.operator != null ? obj.operator : "NA",
              Date: obj.created_at !== null ? obj.created_at : "Update Soon",
            };
          });
          console.log(finalData);
          var options = {
            fieldSeparator: ",",
            quoteStrings: '"',
            decimalseparator: ".",
            showLabels: false,
            showTitle: false,
            // title: 'CSV DATA',
            useBom: true,
            noDownload: false,
            headers: ["No", "Number", "Caller", "Circle", "Operator", "Date"],
          };
          console.log(finalData);

          new ngxCsv(finalData, csvFileName, options);
        } else if (res.status == 202) {
          alert("No data found");
          ///if data greater than limit schedule for csv file by backend
          // Swal.fire({
          //   position: "top",
          //   icon: "info",
          //   title: res.message,
          //   showConfirmButton: false,
          //   timer: 2000,
          // });
        }
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );
  }
}
