import { Injectable } from "@angular/core";
import { BehaviorSubject } from "rxjs/internal/BehaviorSubject";
import { GlobalConstants } from "src/app/global_constant";

@Injectable({
  providedIn: "root",
})
export class TokenAuthService {
  url = GlobalConstants.url;
  user_details: BehaviorSubject<any> = new BehaviorSubject(this.storeUser());

  private tokenIssuer = {
    login: this.url + "signin",
    register: this.url + "signup",
  };

  constructor() {}

  ////store token in session
  setTokenStorage(token) {
    // sessionStorage.setItem("auth_token", token);
    localStorage.setItem("auth_token", token);
  }
  getJwtToken() {
    // return sessionStorage.getItem("auth_token");

    return localStorage.getItem("auth_token");
  }
  storeUser() {
    return localStorage.getItem("auth_token");
  }

  // Validate token
  validateToken() {
    const token = this.getJwtToken();
    if (token) {
      const payload = this.payload(token);
      if (payload) {
        return Object.values(this.tokenIssuer).indexOf(payload.iss) > -1
          ? true
          : false;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  payload(token) {
    const jwtPayload = token.split(".")[1];
    // console.log("jwt", jwtPayload);

    return JSON.parse(atob(jwtPayload));
  }

  // User state
  isSignedin() {
    return this.validateToken();
  }

  // Destroy token
  destroyToken() {
    localStorage.removeItem("username");
    sessionStorage.removeItem("user_id");
    localStorage.removeItem("auth_token");
    localStorage.removeItem("user_id");
  }
}
