import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { SmsServiceService } from "src/app/shared/sms-service.service";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";
import { map } from "rxjs";
import { MyFilterPipe } from "src/app/pipe/my-filter.pipe";
import { JwtService } from "./../../shared/jwt.service";
import { Location } from "@angular/common";

export class Sender_details {
  id: any;
  // country: any;
  sender: any;
  description: any;
  pe_id: any;
  status: any;
  submited_at: any;
}

@Component({
  selector: "app-senderid",
  templateUrl: "./senderid.component.html",
  styleUrls: ["./senderid.component.css"],
  providers: [MyFilterPipe],
})
export class SenderidComponent implements OnInit {
  senderidForm: FormGroup;
  editSenderidForm: FormGroup;
  senderid_details: Sender_details[];
  msg = null;
  err = null;
  user_id: any;
  url = GlobalConstants.url;
  sender_id = null;
  searchedKeyword: string;

  constructor(
    public fb: FormBuilder,
    public smsService: SmsServiceService,
    private http: HttpClient,
    public jwtService: JwtService,
    public location: Location
  ) {}

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("senderid", this.user_id);

    this.senderidForm = this.fb.group({
      // id: [this.user_id],
      // senderid_country: ["India"],
      senderid: [""],
      senderid_description: [""],
      senderid_peid: [""],
      // senderid_upload_doc: [""],
    });

    this.editSenderidForm = this.fb.group({
      // id: [this.user_id],
      // senderid_country: [""],
      senderid: [""],
      senderid_description: [""],
      senderid_peid: [""],
      // senderid_upload_doc: ["Mahesh"],
    });

    //get user sender ids
    this.smsService.getDetSenderID().subscribe(
      (ress: any) => {
        this.senderid_details = ress;
        console.log(ress);
      },
      (error) => {
        console.log(error);

        // this.jwtService.error(error);
      }
    );
  }

  //for document upload
  onFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.senderidForm.patchValue({
        fileSource: file,
      });
    }
  }

  //apply for  new sender id
  onSenderIDApply() {
    console.log(this.senderidForm.value);
    this.smsService.applySenderID(this.senderidForm.value).subscribe(
      (res) => {
        this.ngOnInit(), (this.msg = res);
        console.log(res);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: this.msg.Message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
        alert(this.err);
      }
      // ,
      // () => {
      //   this.senderidForm.reset({
      //     id: this.user_id,
      //   });
      // }
    );
  }

  //seder id delete option
  senderDelete(sender_id_primary) {
    console.log(sender_id_primary);
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.http
          .get(this.url + "delete_senderId/" + sender_id_primary)
          .subscribe((res) => {
            this.ngOnInit(), console.log("res", res);
          });
        // Swal.fire({
        //   title: "Deleted!",
        //   text: "Your senderID has been deleted.",
        //   showConfirmButton: false,
        //   timer: 1000,
        // });
      }
    });
  }

  //edit sender id options
  senderEdit(details, senderid_id: number) {
    this.sender_id = senderid_id;
    this.editSenderidForm.patchValue({
      // senderid_country: details.country,
      senderid: details.sender,
      senderid_description: details.description,
      senderid_peid: details.pe_id,
      // senderid_upload_doc: "mahesh",
    });
    console.log(details);
  }

  senderEditSubmit() {
    console.log(this.editSenderidForm.value);
    console.log(this.sender_id);
    this.smsService
      .editSenderID(this.sender_id, this.editSenderidForm.value)
      .subscribe(
        (res) => {
          this.ngOnInit(), (this.msg = res);
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: this.msg.Message,
            showConfirmButton: false,
            timer: 1000,
          });
        },
        (error) => {
          this.err = error.error;
          // Swal.fire({
          //   position: "center",
          //   icon: "error",
          //   // title: this.err.Status,
          //   title: "Field due to improper contain",
          //   showConfirmButton: false,
          //   timer: 1000,
          // });
        },
        () => {
          // this.editSenderidForm.reset({
          //   'id':this.user_id,
          // });
        }
      );
  }
  //sort filter by status in temp
  sortByStatus(e: any) {}

  getuserdet(data: any) {
    console.log(data);
  }
}
