import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { SmsServiceService } from "src/app/shared/sms-service.service";
import { JwtService } from "./../../shared/jwt.service";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { Location } from "@angular/common";
import { ngxCsv } from "ngx-csv/ngx-csv";
import * as moment from "moment";

export class Log_Report {
  campaign_id: any;
  dlr_mask: any;
  total_sms: any;
  submit_date: any;
  receivers: any;
  done_date: any;
  description: any;
  error_code: any;
}

@Component({
  selector: "app-logs",
  templateUrl: "./logs.component.html",
  styleUrls: ["./logs.component.css"],
})
export class LogsComponent implements OnInit {
  user_id: any;
  campaign_id = "";
  resend_form: FormGroup;
  logs_report: Log_Report[];
  err = null;
  msg = null;
  //pagination
  p: number = 1;
  count: number = 10;
  searchedKeyword: string;

  constructor(
    private route: ActivatedRoute,
    public smsService: SmsServiceService,
    public jwtService: JwtService,
    public fb: FormBuilder,
    public location: Location
  ) {
    route.paramMap.subscribe((param) => {
      this.campaign_id = param.get("id");
    });
  }

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("logs", this.user_id);

    //get logs details
    this.smsService.logsReport(this.campaign_id).subscribe(
      (res: any) => {
        this.logs_report = res.data;
        console.log(res);
      }
      // (error) => {
      //   this.jwtService.error(error);
      // }
    );
  }

  resendSms(val: any) {
    console.log(val);
    if (val === "") {
      Swal.fire({
        position: "top-end",
        icon: "warning",
        title: "please select any status",
        showConfirmButton: false,
        timer: 1000,
      });
    } else {
      //resend sms form
      this.resend_form = this.fb.group({
        campaign_id: [this.campaign_id],
        status: [val],
      });
      this.smsService.resendSms(this.resend_form.value).subscribe(
        (res) => {
          this.ngOnInit(), (this.msg = res);
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: this.msg.Message,
            showConfirmButton: false,
            timer: 1000,
          });
        },
        (error) => {
          this.err = error.error;
          // alert(this.err.error);
        }
        // () => {
        //   this.fund_transfer.reset({
        //     parent_id: this.user_id,
        //   });
        // }
      );
    }
  }

  searchLogs(val: any) {
    console.log(val);
    // val["user_id"] = this.user_id;
    val["campaign_id"] = this.campaign_id;
    console.log(val);

    this.smsService.searchCampaignLogs(val).subscribe(
      (res: any) => {
        console.log(res);
        if (res.data.length > 0) {
          this.logs_report = res.data;
        } else {
          this.ngOnInit();
          Swal.fire({
            position: "top",
            title: "No data Found ",
            showConfirmButton: false,
            timer: 1000,
          });
        }

        // if (res.length > 0) {
        //   console.log(res);
        //   this.campaign_report = res;
        // } else {
        //   this.showReport = false;
        //   // this.showFilterReport = false;
        //   this.elseFilter = true;
        // }
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );
  }
  clearFormValue(searchForm) {
    console.log(searchForm.form.value);
    // searchForm.form.value.dlr_mask = "";
    // searchForm.form.value.receivers = "";
    this.ngOnInit();
    searchForm.reset({
      dlr_mask: "",
      receivers: "",
    });
  }

  //export logs
  exportLogs(searchvalue: any) {
    console.log("legth", this.logs_report.length);
    if (this.logs_report.length < 2000) {
      let i = 0;
      // let new_list = this.temp_details;
      let csvFileName = "smsreport" + moment().format("YYYYMMDDSSMMHH");
      let finalData = this.logs_report.map(function (obj) {
        return {
          No: (i = i + 1),
          Receivers: obj.receivers,
          Status:
            obj.dlr_mask == 923
              ? "Submitted"
              : obj.dlr_mask == 2
              ? "Failed"
              : obj.dlr_mask == 1
              ? "Delivered"
              : obj.dlr_mask == 8
              ? "Sent"
              : // : obj.dlr_mask == 5
                // ? "Rejected&Block"
                "Rejected&Block",
          SubmitDate:
            obj.submit_date !== null ? obj.submit_date : "Update Soon",
          DoneDate: "update soon", //obj.done_date !== null ? obj.done_date : "Update Soon",
          ErrorCode: "update soon", //obj.error_code !== null ? obj.error_code : "Update Soon",
          Description: "update soon", // obj.description !== null ? obj.description : "Update Soon",
        };
      });
      console.log(finalData);
      var options = {
        fieldSeparator: ",",
        quoteStrings: '"',
        decimalseparator: ".",
        showLabels: false,
        showTitle: false,
        // title: 'CSV DATA',
        useBom: true,
        noDownload: false,
        headers: [
          "S.No",
          "Receivers",
          "Status",
          "SubmitDate",
          "DoneDate",
          "ErrorCode",
          "Description",
        ],
        // headers: ["S.No", "Name", "Number", "email"],
      };
      new ngxCsv(finalData, csvFileName, options);
    } else {
      //request for data greater than 2000
      searchvalue["campaign_id"] = this.campaign_id;
      console.log(searchvalue);

      this.smsService.logsExport(searchvalue).subscribe(
        (res: any) => {
          console.log(res);
          if (res.status == 201) {
            /////if data is less than our limit than download instantly
            Swal.fire({
              position: "top",
              icon: "info",
              title: res.message,
              showConfirmButton: false,
              timer: 2000,
            });
            let i = 0;
            // let new_list = this.temp_details;
            let csvFileName = "smsreport" + moment().format("YYYYMMDDSSMMHH");
            let finalData = res.data.map(function (obj) {
              return {
                No: (i = i + 1),
                Receivers: obj.receivers,
                Status:
                  obj.dlr_mask == 923
                    ? "Submitted"
                    : obj.dlr_mask == 2
                    ? "Failed"
                    : obj.dlr_mask == 1
                    ? "Delivered"
                    : obj.dlr_mask == 8
                    ? "Sent"
                    : // : obj.dlr_mask == 5
                      // ? "Rejected&Block"
                      "Rejected&Block",
                SubmitDate:
                  obj.submit_date !== null ? obj.submit_date : "Update Soon",
                DoneDate: "update soon", //obj.done_date !== null ? obj.done_date : "Update Soon",
                ErrorCode: "update soon", //obj.error_code !== null ? obj.error_code : "Update Soon",
                Description: "update soon", // obj.description !== null ? obj.description : "Update Soon",
              };
            });
            console.log(finalData);
            var options = {
              fieldSeparator: ",",
              quoteStrings: '"',
              decimalseparator: ".",
              showLabels: false,
              showTitle: false,
              // title: 'CSV DATA',
              useBom: true,
              noDownload: false,
              headers: [
                "S.No",
                "Receivers",
                "Status",
                "SubmitDate",
                "DoneDate",
                "ErrorCode",
                "Description",
              ],
              // headers: ["S.No", "Name", "Number", "email"],
            };
            new ngxCsv(finalData, csvFileName, options);
          } else if (res.status == 202) {
            ///if data greater than limit schedule for csv file by backend
            Swal.fire({
              position: "top-end",
              icon: "info",
              title: res.message,
              showConfirmButton: false,
              timer: 2000,
            });
          }
        },
        (error) => {
          // this.jwtService.error(error);
        }
      );
    }
  }
}
