import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup,ReactiveFormsModule } from '@angular/forms';
import { throwError } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { JwtService } from '../../shared/jwt.service';

@Component({
  selector: 'app-update-password',
  templateUrl: './update-password.component.html',
  styleUrls: ['./update-password.component.scss'],
})
export class UpdatePasswordComponent implements OnInit {
  updatePwd: FormGroup;
  errors = null;

  constructor(
    public fb: FormBuilder,
    public activatedRoute: ActivatedRoute,
    public jwtService: JwtService,
    public router: Router
  ) {
    let id = sessionStorage.getItem('id');
    // let id = localStorage.getItem('id');
    // console.log(username);
    console.log(id);

    this.updatePwd = this.fb.group({
      // email: [''],
      // username: [username],
      id: [id],
      // current_password: [''],
      password: [''],
      password_confirmation: [''],
      // passwordToken: [''],
    });
    // activatedRoute.queryParams.subscribe((params) => {
    //   this.updatePwd.controls['passwordToken'].setValue(params['token']);
    // });
  }

  ngOnInit(): void {}

  onSubmit() {
    // var a = this.updatePwd.value;
    // console.log(a);

    this.jwtService.updatePassword(this.updatePwd.value).subscribe(
      (result) => {
        alert('Password updated successfully');
        console.log(this.updatePwd.value);
        this.updatePwd.reset();
        this.router.navigate(['accountsetting']);
      },
      (error) => {
        this.handleError(error);
      }
    );

    // this.jwtService.password(this.updatePwd.value).subscribe(
    //   (res) => {
    //     console.log(res);
    //   },
    //   (error) => {
    //     this.handleError(error);
    //   }
    // );
  }

  handleError(error) {
    let errorMsg = '';
    if (error.error instanceof ErrorEvent) {
      errorMsg = `Error: ${error.error.message}`;
    } else {
      errorMsg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(errorMsg);
  }
}
