import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { GlobalConstants } from "src/app/global_constant";
import { Observable, Subject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class PhonebookService {
  url = GlobalConstants.url;
  // user_id =GlobalConstants.user_id;
  // user_id_local =GlobalConstants.user_id_local;

  //send select phonebook name to sms form
  private sharedphonebook = new Subject<any>();
  sendDataOnSelect(data: any) {
    this.sharedphonebook.next(data);
  }
  getPhonebook(): Observable<any> {
    return this.sharedphonebook.asObservable();
  }
  /////end//////

  constructor(private http: HttpClient) {}

  //createphonebook
  public newphonebookSave(data): Observable<any> {
    return this.http.post<any>(this.url + "phonebookSave", data);
  }
  //get phonebook list
  public getPhonebookList() {
    return this.http.get<any>(this.url + "sms");
  }

  // add column in phonebook
  public column_add(data) {
    return this.http.post<any>(this.url + "phonebookColumn", data);
  }
  //get column list
  public getColumnList() {
    return this.http.get<any>(this.url + "getColumn");
  }
  //edit column_edit
  public column_edit(data) {
    return this.http.post<any>(this.url + "phonebookColumnEdit", data);
  }

  //create new contact in phonebook
  public create_contact(data) {
    return this.http.post<any>(this.url + "phonebookValue", data);
  }

  public getcontactlist(group_id) {
    return this.http.get<any>(this.url + "contactList/" + group_id);
  }

  public contact_import(data) {
    return this.http.post<any>(this.url + "phonebookimport", data);
  }

  public phonebook_name(group_id) {
    return this.http.get<any>(this.url + "phonebookName/" + group_id);
  }

  // public contact_export(data) {
  //   return this.http.get<any>(this.url + "export-users", data);
  // }\

  public contactEditDetail(contact_id) {
    return this.http.get<any>(this.url + "contactEditDetail/" + contact_id);
  }

  public contact_edit(data) {
    return this.http.post<any>(this.url + "contactEdit", data);
  }
}
