import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup } from "@angular/forms";
import { Router } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";
import { SmsServiceService } from "src/app/shared/sms-service.service";
import Swal from "sweetalert2/dist/sweetalert2.js";

export class User {}

@Component({
  selector: "app-dlt-information",
  templateUrl: "./dlt-information.component.html",
  styleUrls: ["./dlt-information.component.scss"],
})
export class DltInformationComponent implements OnInit {
  newdlt: FormGroup;
  user: User;
  msg = null;
  err = null;
  display = "none";

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public jwtService: JwtService,
    public smsService: SmsServiceService
  ) {
    let id = sessionStorage.getItem("id");
    // let id = localStorage.getItem('id');
    // console.log(username);
    // console.log(id);

    //new dlt infor. saved
    this.newdlt = this.fb.group({
      id: [id],
      // username: [username],
      pe_id: [],
      sender_id: [],
      header_id: [],
      template_id: [],
      content: [],
    });
  }

  ngOnInit(): void {}

  openModal() {
    this.display = "block";
  }
  onCloseHandled() {
    this.display = "none";
  }

  onSubmit() {
    this.smsService.newDltSaved(this.newdlt.value).subscribe(
      (res) => {
        this.msg = res;
        Swal.fire({
          position: "top-center",
          icon: "success",
          title: "Successfully added!",
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error.message;
      },
      () => {
        this.newdlt.reset();
      }
    );
  }
}
