import { Component, OnInit } from "@angular/core";
import { LoginInAuthService } from "src/app/login-in-auth.service";
import { Router } from "@angular/router";
import { ResellerService } from "src/app/shared/reseller/reseller.service";

@Component({
  selector: "app-mainpage",
  templateUrl: "./mainpage.component.html",
  styleUrls: ["./mainpage.component.scss"],
})
export class MainpageComponent implements OnInit {
  url: any = "";
  url_image = "http://bulk24sms.in:8000/storage/";
  data = {
    domain: "",
    email: "",
    address: "",
    contact_number: "",
  };
  social_links = {
    facebook: "",
    twitter: "",
    linkedin: "",
    googleplus: "",
  };
  contact_us = {
    name: "",
    number: "",
    email: "",
    message: "",
  };

  aboutUsImage: string;
  default_AboutUs = "assets/white-label-img/who-we-are-1.png";
  about_desc: string;
  default_logo = "assets/white-label-img/logo-footer.png";
  logoImage: string;

  iconImage: string;

  bannerImage: string;

  service1: string;
  service2: string;
  service3: string;

  constructor(
    private auth: LoginInAuthService,
    public router: Router,
    private reseller: ResellerService
  ) {}

  ngOnInit(): void {
    if (this.auth.LoggedIn) {
      this.router.navigate(["/dashboard"]);
    }

    this.url = window.location.host;
    // this.url = "bulk24sms.in";
    console.log(this.url);

    this.reseller.getWhiteLabelData(this.url).subscribe(
      (res) => {
        console.log(res);

        if (res.status == 200) {
          this.data = res.data[0];
          //sociallink
          var social_link = JSON.parse(res.data[0].social_links);
          this.social_links = social_link;
          this.social_links.facebook = social_link.facebook;
          this.social_links.twitter = social_link.twitter;
          this.social_links.linkedin = social_link.linkedin;
          this.social_links.googleplus = social_link.googleplus;

          //about us
          this.aboutUsImage = res.data[0].about_image;
          this.about_desc = res.data[0].about_desc;

          console.log(res.data[0].about_image);

          if (res.data[0].about_image !== null) {
            console.log("mahesh");
          }

          //2  services
          this.service1 = res.data[0].service1;
          this.service2 = res.data[0].service2;
          this.service3 = res.data[0].service3;

          this.logoImage = res.data[0].logo;
          this.iconImage = res.data[0].icon;
          this.bannerImage = res.data[0].banner_image;

          console.log(this.social_links);
          // if (res.status == 401) {
          //   alert("domain not found");
          // }
        }
      },
      (error) => {
        if (error.status == 401 || 403) {
          alert("not found");
        }
        console.log(error);
      }
    );
  }

  contactForm() {
    console.log(this.contact_us);
  }
}
