import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

import { LoginComponent } from "./components/login/login.component";
import { RegisterComponent } from "./components/register/register.component";
import { ProfileComponent } from "./components/profile/profile.component";

import { ResetPasswordComponent } from "./components/reset-password/reset-password.component";
import { UpdatePasswordComponent } from "./components/update-password/update-password.component";
import { AuthGuard } from "./auth.guard";
import { LoginInAuthGuard } from "./login-in-auth.guard";
import { MainpageComponent } from "./components/mainpage/mainpage.component";

import { HeaderNewComponent } from "./bulk24/header-new/header-new.component";
import { AccountsettingComponent } from "./bulk24/accountsetting/accountsetting.component";
import { SmsComponent } from "./bulk24/sms/sms.component";
import { NewSendFormComponent } from "./bulk24/new-send-form/new-send-form.component";
import { UrlshorterComponent } from "./bulk24/urlshorter/urlshorter.component";
import { DashboardComponent } from "./bulk24/dashboard/dashboard.component";
import { AppsServicesComponent } from "./bulk24/apps-services/apps-services.component";
import { GlobalreportComponent } from "./bulk24/globalreport/globalreport.component";
import { FullreportComponent } from "./bulk24/fullreport/fullreport.component";
import { UsermanagementComponent } from "./bulk24/usermanagement/usermanagement.component";
import { DeliveryreportComponent } from "./bulk24/deliveryreport/deliveryreport.component";
//report new
import { ReportComponent } from "./bulk24/report/report.component";
import { CampaignFullComponent } from "./bulk24/campaign-full/campaign-full.component";
import { LogsComponent } from "./bulk24/logs/logs.component";
import { LinkTrackingComponent } from "./bulk24/link-tracking/link-tracking.component";

//new sender and templates
import { SenderidComponent } from "./bulk24/senderid/senderid.component";
import { TemplatesComponent } from "./bulk24/templates/templates.component";
import { ManagewebsiteComponent } from "./bulk24/managewebsite/managewebsite.component";
import { WhitelabelComponent } from "./bulk24/whitelabel/whitelabel.component";
import { WalletaccountComponent } from "./bulk24/walletaccount/walletaccount.component";
import { OtpenterComponent } from "./components/otpenter/otpenter.component";
import { OtpsubmitComponent } from "./components/otpsubmit/otpsubmit.component";
import { ResellerComponent } from "./bulk24/reseller/reseller.component";
import { PhonebookComponent } from "./bulk24/phonebook/phonebook.component";
import { CreateGroupComponent } from "./bulk24/create-group/create-group.component";
import { ImportContactsComponent } from "./bulk24/import-contacts/import-contacts.component";
import { DltInformationComponent } from "./bulk24/dlt-information/dlt-information.component";
import { CampaignsSmsComponent } from "./bulk24/campaigns-sms/campaigns-sms.component";
import { ImportConComponent } from "./bulk24/import-con/import-con.component";
//-----virtual service-----------------------------------------------------------------------------
import { AddservicesComponent } from "./virtualservice/addservices/addservices.component";
import { ReportvirtualComponent } from "./virtualservice/reportvirtual/reportvirtual.component";
import { ViewComponent } from "./virtualservice/view/view.component";
//-------missedcall route------------------------------------------------------------------------------
import { AddmissedcallComponent } from "./missedCall/addmissedcall/addmissedcall.component";
import { ReportmissedcallComponent } from "./missedCall/reportmissedcall/reportmissedcall.component";

const routes: Routes = [
  {
    path: "",
    // redirectTo: "/",
    pathMatch: "full",
    component: MainpageComponent,
    // canActivate: [LoginInAuthGuard],
  },
  { path: "signin", component: LoginComponent },
  { path: "signup", component: RegisterComponent },
  {
    path: "",
    component: MainpageComponent,
  },

  {
    path: "user-profile",
    component: ProfileComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "update-password",
    component: UpdatePasswordComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "reset-password",
    component: ResetPasswordComponent,
    // canActivate: [AuthGuard],
  },

  {
    path: "otpenter",
    component: OtpenterComponent,
    // canActivate: [AuthGuard],
  },
  {
    path: "otpsubmit",
    component: OtpsubmitComponent,
    // canActivate: [AuthGuard],
  },

  //////////////////////////bulk24//////////////////////
  //HEADER
  {
    path: "header_new",
    component: HeaderNewComponent,
    canActivate: [AuthGuard],
  },
  //phonebook routes
  {
    path: "phonebook",
    component: PhonebookComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "create_group/:id",
    component: CreateGroupComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "import_contacts/:id",
    component: ImportContactsComponent,
    canActivate: [AuthGuard],
  },
  //sms send routes news
  {
    path: "dashboard",
    component: DashboardComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "services",
    component: AppsServicesComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "glb_report",
    component: GlobalreportComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "Link_Tracking",
    component: LinkTrackingComponent,
    canActivate: [AuthGuard],
  },
  //reseller/MANAGE WEBSITE sections
  {
    path: "whitelabel",
    component: WhitelabelComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "managewebsite/:id",
    component: ManagewebsiteComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "usermanagement",
    component: UsermanagementComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "reseller/:id",
    component: ResellerComponent,
    canActivate: [AuthGuard],
  },
  //wallet
  {
    path: "walletaccount",
    component: WalletaccountComponent,
    canActivate: [AuthGuard],
  },
  //profile ACCOUNTS
  {
    path: "accountsetting",
    component: AccountsettingComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "urlshorter",
    component: UrlshorterComponent,
    canActivate: [AuthGuard],
  },

  /////////delete extra route none use
  //extrassass
  {
    path: "campaigns_sms",
    component: CampaignsSmsComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "dlt_information",
    component: DltInformationComponent,
    canActivate: [AuthGuard],
  },

  //old sms send
  { path: "sms", component: SmsComponent, canActivate: [AuthGuard] },

  {
    path: "fullreport/:id",
    component: FullreportComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "deliveryreport",
    component: DeliveryreportComponent,
    canActivate: [AuthGuard],
  },
  //------------------------------------sms rounting start-------------------------------------------------------
  // {
  //   path: "sms",
  //   children: [
  {
    path: "new_send_form",
    component: NewSendFormComponent,
    canActivate: [AuthGuard],
  },
  //new senderid and templates
  { path: "senderid", component: SenderidComponent, canActivate: [AuthGuard] },
  {
    path: "templates",
    component: TemplatesComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "template_import",
    component: ImportConComponent,
    canActivate: [AuthGuard],
  },
  //dlr report new component//
  { path: "report", component: ReportComponent, canActivate: [AuthGuard] },
  {
    path: "sms_statistics/:id",
    component: CampaignFullComponent,
    canActivate: [AuthGuard],
  },
  { path: "logs/:id", component: LogsComponent, canActivate: [AuthGuard] },
  // ],
  // },

  //------------------------------------sms rounting end-------------------------------------------------------
  //------------------------------------voice rounting-------------------------------------------------------
  {
    path: "voice",
    loadChildren: () =>
      import("./voice/voice.module").then((m) => m.VoiceModule),
  },
  //---------------------------------virtual service routing-------------------------------------------------
  {
    path: "virsrv",
    children: [
      {
        path: "addvirtualservice",
        component: AddservicesComponent,
        canActivate: [AuthGuard],
      },
      {
        path: "reportvirtual",
        component: ReportvirtualComponent,
        canActivate: [AuthGuard],
      },
      {
        path: "statisticsVirtual",
        component: ViewComponent,
        canActivate: [AuthGuard],
      },
    ],
  },
  //------------------------------missed call routing----------------------------------------
  {
    path: "msdcall",
    children: [
      {
        path: "addmissedcall",
        component: AddmissedcallComponent,
        canActivate: [AuthGuard],
      },
      {
        path: "reportmissedcall",
        component: ReportmissedcallComponent,
        canActivate: [AuthGuard],
      },
      // { path: "statisticsMC", component: ReportmissedcallComponent,canActivate: [AuthGuard] },
    ],
  },

  //-------Emailer-----------------------------//

  {
    path: "emailer",
    loadChildren: () =>
      import("./emailer/emailer.module").then((m) => m.EmailerModule),
  },
  //-------Emailer-----------------------------//

  // {
  //   path: "statisticsMC",
  //   component: ReportmissedcallComponent,
  //   canActivate: [AuthGuard],
  // },

  // {
  //   path: "vrt",
  //   loadChildren: () =>
  //     import("./virtual-services/virtual-services.module").then(
  //       (m) => m.VirtualServicesModule
  //     ),
  //   // component: VirtualServicesModule,
  //   // children: [
  //   //   { path: "add_vrt_srv", component: AddListComponent },
  //   //   { path: "virtual_report", component: ReportVirtualComponent },
  //   //   { path: "virtual_view", component: ViewVirtualComponent },
  //   // ],
  // },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
