import { Component, OnInit } from "@angular/core";
import { VirtualserviceService } from "src/app/shared/virtualservice-service/virtualservice.service";
import { FormBuilder, FormGroup } from "@angular/forms";
import Swal from "sweetalert2/dist/sweetalert2.js";

@Component({
  selector: "app-addservices",
  templateUrl: "./addservices.component.html",
  styleUrls: ["./addservices.component.css"],
})
export class AddservicesComponent implements OnInit {
  addKeyword: FormGroup;
  // add_keyword: FormGroup;
  isCheckedWeb = false;
  isCheckedSMS = false;
  isCheckedEmail = false;
  virtual_number_list: [];
  keyword_details: [];
  add_keyword = {
    keywords: "",
    number: "",
    status: "",
    description: "",
    webhook_url: "",
    sms_response: {
      sender: "",
      template_id: "",
      message: "",
    },
    email_response: {
      email: "",
      subject: "",
      email_content: "",
    },
  };
  edit_keyword = {
    id: "",
    keywords: "",
    number: "",
    status: "",
    description: "",
    webhook_url: "",
    sms_response: {
      sender: "",
      template_id: "",
      message: "",
    },
    email_response: {
      email: "",
      subject: "",
      email_content: "",
    },
  };

  selectedNumber(event: any) {
    console.log(event);

    console.log(event.target.value.toString().length);

    if (event.target.value.toString().length > 9) {
      alert("You have select Long Code Services");
    } else {
      alert("You have select Short Code Service");
    }
  }

  addWebhook(event: any) {
    if (event.target.name == "email_response") {
      this.isCheckedEmail = event.target.checked;
      console.log(event.target.name);
    }
    if (event.target.name == "sms_response") {
      this.isCheckedSMS = event.target.checked;
    }
    if (event.target.name == "webhook") {
      this.isCheckedWeb = event.target.checked;
    }
    console.log(event);

    console.log(event.target.checked);
  }

  constructor(
    public virtualService: VirtualserviceService,
    public fb: FormBuilder
  ) {}

  ngOnInit(): void {
    // //add keyword
    // this.addKeyword = this.fb.group({
    //   number: [""],
    //   keywords: [],
    //   status: [""],
    //   description: [],
    //   webhook_url: [],
    //   sms_response: this.fb.group({
    //     sender: [""],
    //     template_id: [""],
    //     message: [""],
    //   }),
    //   email_response: {
    //     email: [""],
    //     subject: [""],
    //     email_content: [""],
    //   },
    // });
    //get virtual number
    this.virtualService.getVirtualNoList().subscribe(
      (res: any) => {
        // this.phonebook_name = res_name[0].phonebook_name;
        // this.phonebook_count = res_name[0].count;
        console.log(res);
        this.virtual_number_list = res.data;
      },
      (error) => {
        console.log(error);
        // if (error.status === 401) {
        //   alert(error.error.message);
        //   this.jwtService.logout();
        // }
      }
    );

    //get keywords

    this.virtualService.getKeyword().subscribe(
      (res: any) => {
        console.log(res);
        console.log(res.data);
        this.keyword_details = res.data;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  onAddKeyword() {
    // console.log(this.addKeyword.value);
    if (this.add_keyword.keywords == "" || this.add_keyword.number == "") {
      alert("Please add keyword and select Number");
    } else {
      this.virtualService.addKeyword(this.add_keyword).subscribe(
        (res) => {
          if (res) {
            // alert("Added Successfully");
            this.ngOnInit(), console.log(res);
          }
        },
        (error) => {
          console.log(error);
        }
      );
    }
  }
  editKeyword(key_det: any) {
    console.log(key_det);
    var sms_res = JSON.parse(key_det.sms_response);
    // var email_res = JSON.parse(key_det.email_response);

    this.isCheckedWeb = true;
    this.isCheckedSMS = true;
    // this.isCheckedEmail = true;
    this.edit_keyword.id = key_det.id;
    this.edit_keyword.keywords = key_det.keywords;
    this.edit_keyword.number = key_det.number;
    this.edit_keyword.status = key_det.status;
    this.edit_keyword.description = key_det.description;
    this.edit_keyword.webhook_url = key_det.webhook_url;
    this.edit_keyword.sms_response.sender = sms_res.sender;
    this.edit_keyword.sms_response.template_id = sms_res.template_id;
    this.edit_keyword.sms_response.message = sms_res.message;
    // this.edit_keyword.email_response.email = email_res.email;
    // this.edit_keyword.email_response.subject = email_res.subject;
    // this.edit_keyword.email_response.email_content = email_res.email_content;
  }
  editKeywordSend() {
    console.log(this.edit_keyword);
    this;
    this.virtualService.editKeywords(this.edit_keyword).subscribe(
      (res) => {
        this.ngOnInit(), console.log(res);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  //delete
  deleteKeyword(id: number) {
    console.log(id);

    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.virtualService.deleteKeywords(id).subscribe((res) => {
          this.ngOnInit(), console.log("res", res);
          if (res) {
            Swal.fire({
              title: "Deleted!",
              text: "Keyword delete Successfully",
              showConfirmButton: false,
              timer: 1000,
            });
          }
        });
      }
    });
  }
}
