import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";

@Injectable({
  providedIn: "root",
})
export class MissedcallService {
  url = GlobalConstants.url;
  constructor(private http: HttpClient) {}

  //get missedcall no list
  public getMissedCallNo() {
    return this.http.get<any>(this.url + "get_missedCall_number");
  }

  //add keyword
  public addMissedCallNo(data) {
    return this.http.post<any>(this.url + "insert_missed_call", data);
  }

  //get missedcall  details/list
  public getMissedCallList() {
    return this.http.get<any>(this.url + "get_missedCall_list");
  }

  //delete number from the list
  public deleteMissedCallNo(id: number) {
    return this.http.get<any>(this.url + "delete_missed_call/" + id);
  }
  //edit number
  editMissedCallNo(data) {
    return this.http.post<any>(this.url + "edit_missed_call", data);
  }

  //report
  get_M_C_Report() {
    return this.http.get<any>(this.url + "missed_call_report");
  }

  //filter
  getFilterReport(data) {
    return this.http.post<any>(this.url + "filter_missed_call", data);
  }
}
