import { Component, OnInit } from "@angular/core";
import { FormBuilder } from "@angular/forms";
import { Router } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import Swal from "sweetalert2/dist/sweetalert2.js";

export class User {
  mail_balance: any;
  rcs_balance: any;
  wallet_amount: any;
  pr_sms_balance: any;
  tr_sms_balance: any;
  short_code_balance: any;
  long_code_balance: any;
  pr_voice_balance: any;
  tr_voice_balance: any;
}

@Component({
  selector: "app-walletaccount",
  templateUrl: "./walletaccount.component.html",
  styleUrls: ["./walletaccount.component.scss"],
})
export class WalletaccountComponent implements OnInit {
  user: User;
  user_id: any;
  closeResult = "";

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public jwtService: JwtService,
    private modalService: NgbModal
  ) {}

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("wallet", this.user_id);
    //wallet details
    this.jwtService.profile().subscribe(
      (res: any) => {
        this.user = res;
        console.log(res);
      },
      (error) => {
        console.log(error);
        if (error.status === 401 || 403) {
          Swal.fire({
            position: "top-end",
            icon: "warning",
            title: "Session Expire please login again",
            // title: error.error.message,
            showConfirmButton: false,
            timer: 1000,
          });
          this.jwtService.logout();
        }
      }
    );
  }

  open(content) {
    this.modalService
      .open(content, { ariaLabelledBy: "modal-basic-title" })
      .result.then(
        (result) => {
          this.closeResult = `Closed with: ${result}`;
        },
        (reason) => {
          this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        }
      );
  }
  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return "";
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return "";
      // return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }
}
