import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { MissedcallService } from "src/app/shared/missedcallservice/missedcall.service";
import Swal from "sweetalert2/dist/sweetalert2.js";

@Component({
  selector: "app-addmissedcall",
  templateUrl: "./addmissedcall.component.html",
  styleUrls: ["./addmissedcall.component.css"],
})
export class AddmissedcallComponent implements OnInit {
  addServiceForm: FormGroup;
  editServiceForm: FormGroup;
  missedcallNumber: [];
  missedcallList: [];
  isCheckedWeb = false;
  isCheckedSMS = false;
  isCheckedEmail = false;

  addWebhook(event: any) {
    if (event.target.name == "email_response") {
      this.isCheckedEmail = event.target.checked;
      console.log(event.target.name);
    }
    if (event.target.name == "sms_response") {
      this.isCheckedSMS = event.target.checked;
    }
    if (event.target.name == "webhook") {
      this.isCheckedWeb = event.target.checked;
    }
    console.log(event);

    console.log(event.target.checked);
  }

  constructor(
    public fb: FormBuilder,
    public missedCallService: MissedcallService
  ) {}

  ngOnInit(): void {
    //addnoform
    this.addServiceForm = this.fb.group({
      number: [""],
      status: [""],
      webhook_url: [""],
      sms_response: this.fb.group({
        sender: [""],
        template_id: [""],
        message: [""],
      }),
      // email_response: this.fb.group({
      //   email: [""],
      //   subject: [""],
      //   content: [""],
      // }),
    });

    //addnoform
    this.editServiceForm = this.fb.group({
      id: [],
      number: [""],
      status: [""],
      webhook_url: [""],
      sms_response: this.fb.group({
        sender: [""],
        template_id: [""],
        message: [""],
      }),
      // email_response: this.fb.group({
      //   email: [""],
      //   subject: [""],
      //   content: [""],
      // }),
    });

    //get missed call no
    this.missedCallService.getMissedCallNo().subscribe(
      (res) => {
        console.log(res);
        this.missedcallNumber = res.data;
      },
      (error) => {
        console.log(error);
      }
    );

    //get missed call list
    this.missedCallService.getMissedCallList().subscribe(
      (res) => {
        console.log(res);
        this.missedcallList = res.data;
      },
      (error) => {
        console.log(error);
      }
    );
  }

  onAddMissedService() {
    console.log(this.addServiceForm.value);

    this.missedCallService.addMissedCallNo(this.addServiceForm.value).subscribe(
      (res) => {
        if (res) {
          // alert("Added Successfully");
          this.ngOnInit(), console.log(res);
        }
      },
      (error) => {
        console.log(error);
      }
    );
  }
  editMissedCallNo(details: any) {
    // this.isCheckedEmail=true;
    // this.isCheckedSMS = true;
    // this.isCheckedWeb = true;
    console.log(details);
    var sms_resp = JSON.parse(details.sms_response);
    console.log(sms_resp);

    // var email_resp=JSON.parse(details.email_response);
    this.editServiceForm.patchValue({
      id: details.id,
      number: details.missed_call_number,
      status: details.status,
      webhook_url: details.webhook_url,
      sms_response: {
        sender: sms_resp.sender,
        template_id: sms_resp.template_id,
        message: sms_resp.message,
      },
      // email_response: this.fb.group({
      //   email: email_resp.email,
      //   subject: email_resp.subject,
      //   content: email_resp.content,
      // }),
    });
  }
  editMissedCallNoSave() {
    this.missedCallService
      .editMissedCallNo(this.editServiceForm.value)
      .subscribe(
        (res) => {
          if (res) {
            // alert("Added Successfully");
            this.ngOnInit(), console.log(res);
          }
        },
        (error) => {
          console.log(error);
        }
      );
  }
  deleteMissedCallNo(id: any) {
    console.log(id);
    this.missedCallService.deleteMissedCallNo(id).subscribe(
      (res) => {
        if (res) {
          // alert("Added Successfully");
          // this.ngOnInit(),
          console.log(res);
        }
      },
      (error) => {
        console.log(error);
      }
    );

    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.missedCallService.deleteMissedCallNo(id).subscribe(
          (res) => {
            if (res) {
              // alert("Added Successfully");
              this.ngOnInit(), console.log(res);
            }
          },
          (error) => {
            console.log(error);
            alert(error);
          }
        );
      }
    });
  }
}
