import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { Router } from "@angular/router";
import { GlobalConstants } from "src/app/global_constant";
import { ResellerService } from "src/app/shared/reseller/reseller.service";
import { User_List } from "src/app/bulk24/usermanagement/reseller_user.model";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { JwtService } from "./../../shared/jwt.service";
import { Country } from "src/app/components/register/country.model";

@Component({
  selector: "app-usermanagement",
  templateUrl: "./usermanagement.component.html",
  styleUrls: ["./usermanagement.component.scss"],
})
export class UsermanagementComponent implements OnInit {
  reseller_create: FormGroup;
  url = GlobalConstants.url;
  user_id: any;
  err = null;
  msg = null;
  countrydata: Country[];
  user_lists: User_List[];
  searchedKeyword: string;
  Grid_View_show: boolean;
  List_View_show: boolean;

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public reseller: ResellerService,
    public jwtService: JwtService
  ) {}

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("usermanagements", this.user_id);

    // get lists of user create by reseller
    this.reseller.getResellerUserList().subscribe(
      (res: any) => {
        this.user_lists = res;
        console.log(res);
      },
      (error) => {
        console.log(error);
        if (error.status === 401 || 404) {
          Swal.fire({
            position: "top-end",
            icon: "warning",
            // title: "Session Expire please login again",
            title: error.error.message,
            showConfirmButton: false,
            timer: 1000,
          });
          // this.jwtService.logout();
        }
      }
    );

    //getcountrydata
    this.countrydata = this.jwtService.getcountrylist();

    //create user inside user/reseller
    this.reseller_create = this.fb.group({
      // id: [this.user_id],
      name: [],
      username: [],
      contact_number: [],
      email: [],
      password: [],
      password_confirmation: [],
      company_name: [],
      industry: ["0"],
      country_name: ["+91"],
    });
    //list grid view
    this.List_View_show = false;
    this.Grid_View_show = true;
  }
  //create new user to reseller section
  onSubmit() {
    console.log(this.reseller_create.value);
    this.reseller.createUserByRes(this.reseller_create.value).subscribe(
      (res) => {
        this.ngOnInit(), (this.msg = res);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: this.msg.Status,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
        Swal.fire({
          position: "center",
          icon: "error",
          // title: this.err.Status,
          title: "Username not available",
          showConfirmButton: false,
          timer: 1000,
        });
      }
      // ,
      // () => {
      //   this.reseller_create.reset({ id: this.user_id });
      // }
    );
  }

  showList() {
    this.List_View_show = true;
    this.Grid_View_show = false;
  }
  showGrid() {
    this.List_View_show = false;
    this.Grid_View_show = true;
  }
}
