  onFileAboutUs(event: any) {
    const reader = new FileReader();

    if (event.target.files.length > 0) {
      const file1 = event.target.files[0];
      reader.readAsDataURL(file1);
      reader.onload = () => {
        this.aboutUsImage = reader.result as string;
      };

      console.log("about_file", file1);
      this.about_us_image = file1;
    }
  }

