  searchReport(val: any) {
    console.log(val);

    this.smsService.searchCampaignReport(val).subscribe(
      (res: any) => {
        console.log(res);

        if (res.data.length > 0) {
          if (res.data[0].receivers) {
            if (res.data[0].receivers !== null) {
              console.log("NOT NULL");
              console.log(res);

              this.campaign_report = res.data;
              this.showReport = false;
              this.showReportMobile = true;
              this.elseFilter = false;
            }
            // else {
            //   console.log(res[0].receivers);
            //   console.log("NULL");
            //   this.showReport = false;
            //   this.showReportMobile = false;
            //   this.elseFilter = true;
            // }
          } else {
            console.log(res);
            console.log("else condition means not have any number");
            this.campaign_report = res.data;
            this.showReport = true;
            this.showReportMobile = false;
            this.elseFilter = false;
          }
        } else {
          console.log("means no data found with route and date");
          this.showReport = false;
          this.showReportMobile = false;
          this.elseFilter = true;
        }
      },
      (error) => {
        if (error.status == 401) {
          this.jwtService.error(error);
        }
      }
    );
  }

