  exportReport(val: any) {
    console.log(val);

    this.smsService.reportExport(val).subscribe(
      (res: any) => {
        console.log(res);
        if (res.status == 201) {
          let i = 0;
          let csvFileName = "smsreport" + moment().format("YYYYMMDDSSMMHH");
          let finalData = res.data.map(function (obj) {
            return {
              No: (i = i + 1),
              CampaignName: obj.campaign_name,
              CampaignUid: obj.campaign_id,
              SenderID: obj.sender,
              Message: obj.message,
              TotalCredit: obj.credit,
              Receivers: obj.receivers,
              Status:
                obj.dlr_mask == 923
                  ? "Submitted"
                  : obj.dlr_mask == 2
                  ? "Failed"
                  : obj.dlr_mask == 1
                  ? "Delivered"
                  : obj.dlr_mask == 8
                  ? "Sent"
                  : // : obj.dlr_mask == 5
                    // ? "Rejected&Block"
                    "Rejected&Block",
              SubmitDate:
                obj.submit_date !== null ? obj.submit_date : "Update Soon",
              DoneDate: obj.done_date !== null ? obj.done_date : "Update Soon",
            };
          });
          console.log(finalData);
          var options = {
            fieldSeparator: ",",
            quoteStrings: '"',
            decimalseparator: ".",
            showLabels: false,
            showTitle: false,
            // title: 'CSV DATA',
            useBom: true,
            noDownload: false,
            headers: [
              "S.No",
              "CampaignName",
              "CampaignUid",
              "SenderID",
              "Message",
              "TotalCredit",
              "Receivers",
              "Status",
              "SubmitDate",
              "DoneDate",
            ],
            // headers: ["S.No", "Name", "Number", "email"],
          };
          new ngxCsv(finalData, csvFileName, options);
        } else if (res.status == 202) {
          ///if data greater than limit schedule for csv file by backend
          Swal.fire({
            position: "top",
            icon: "info",
            title: res.message,
            showConfirmButton: false,
            timer: 2000,
          });
        }
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );
  }
