import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'myfilter',
  pure: false,
})
export class MyFilterPipe implements PipeTransform {
  transform(senderid_details: any[], filter: any): any {
    if (!senderid_details || !filter) {
      return senderid_details;
    }
    return senderid_details.filter(
      (item) => item.status.indexOf(filter.status) !== -1
    );
  }

  template(temp_details: any[], filter: any): any {
    if (!temp_details || !filter) {
      return temp_details;
    }
    return temp_details.filter(
      (item) => item.status.indexOf(filter.status) !== -1
    );
  }
}
