import { Component, OnInit } from "@angular/core";
import { JwtService } from "../../shared/jwt.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { LoginInAuthService } from "src/app/login-in-auth.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-reset-password",
  templateUrl: "./reset-password.component.html",
  styleUrls: ["./reset-password.component.scss"],
})
export class ResetPasswordComponent implements OnInit {
  currentItem = "Television";
  myForm: FormGroup;
  err = null;
  msg = null;
  // success = null;

  constructor(
    public fb: FormBuilder,
    public jwtService: JwtService,
    private auth: LoginInAuthService,
    public router: Router
  ) {
    this.myForm = this.fb.group({
      username: ["", [Validators.required]],
    });
  }

  ngOnInit(): void {
    if (this.auth.LoggedIn) {
      this.router.navigate(["/user-profile"]);
    }
  }

  onSubmit() {
    this.jwtService.reqPasswordReset(this.myForm.value).subscribe(
      (res) => {
        this.msg = res;
        var user = this.myForm.value;
        console.log(res);

        // console.log(user);

        this.router.navigate(["otpenter"], {
          state: { user },
        });
      },
      (error) => {
        this.err = error.error.error;
        console.log(error.error);
        console.log(error.error.error);
      },
      () => {
        this.myForm.reset();
      }
    );
  }
}
