import { Pipe, PipeTransform } from "@angular/core";

@Pipe({ name: "roundTen" })
export class RoundTenPipe implements PipeTransform {
  transform(value: number): number {
    // return Math.round(value / 70);
    return Math.floor(value / 71 + 1);
  }
}

@Pipe({ name: "roundEnglish" })
export class RoundEngPipe implements PipeTransform {
  transform(value: number): number {
    // return Math.round(value / 70);
    return Math.floor(value / 161 + 1);
  }
}
