import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";

@Injectable({
  providedIn: "root",
})
export class VirtualserviceService {
  url = GlobalConstants.url;
  constructor(private http: HttpClient) {}

  //get virtual no list
  public getVirtualNoList() {
    return this.http.get<any>(this.url + "get_virtual_number");
  }

  //add keyword
  addKeyword(data) {
    return this.http.post<any>(this.url + "insert_keyword", data);
  }

  //get keywordlist
  getKeyword() {
    return this.http.get<any>(this.url + "get_keyword");
  }
  deleteKeywords(id: number) {
    return this.http.get<any>(this.url + "delete_keyword/" + id);
  }
  //update edit keyword
  editKeywords(data) {
    return this.http.post<any>(this.url + "edit_keyword", data);
  }

  //get report/inbox
  getInboxReport() {
    return this.http.get<any>(this.url + "virtual_report");
  }

  //searchfillterEnquries   filter_enquires
  searchEnquires(data) {
    return this.http.post<any>(this.url + "filter_enquires", data);
  }
}
