import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";
import { GlobalConstants } from "src/app/global_constant";
import { ngxCsv } from "ngx-csv/ngx-csv";

@Injectable({
  providedIn: "root",
})
export class SmsServiceService {
  url = GlobalConstants.url;
  // user_id = GlobalConstants.user_id;
  // user_id_local = GlobalConstants.user_id_local;

  constructor(private http: HttpClient) {
    // this.user_id = sessionStorage.getItem('id');
    // console.log('smsservice', this.user_id);
  }

  //save new dlt information
  newDltSaved(data) {
    return this.http.post<any>(this.url + "dlt-info", data);
  }

  //sendsms by panel
  sendsms(data) {
    return this.http.post<any>(this.url + "send-sms", data);
  }
  //info of user/////
  info(): Observable<any> {
    return this.http.get(this.url + "sms");
  }

  // //delivery Report of user
  // deliveryReport(): Observable<any> {
  //   return this.http.get(this.url + 'deliveryReport/' + this.user_id);
  // }
  //campaign report of user
  campaignReport(): Observable<any> {
    return this.http.get(this.url + "report");
  }
  //campaign report after filter
  searchCampaignReport(data) {
    return this.http.post<any>(this.url + "filterReport", data);
  }

  //logs Report of user
  logsReport(compaign_id): Observable<any> {
    return this.http.get(this.url + "logs/" + compaign_id);
  }
  //campaign logs after filter
  searchCampaignLogs(data) {
    return this.http.post<any>(this.url + "logsFilter", data);
  }

  //resend message
  resendSms(data) {
    return this.http.post<any>(this.url + "resendSms", data);
  }

  //full delivery report of user
  getFullReport(campaign_id): Observable<any> {
    return this.http.get(this.url + "fullReport/" + campaign_id);
  }
  //get data for graph
  campaignFull(campaign_id): Observable<any> {
    return this.http.get(this.url + "campaignFull/" + campaign_id);
  }
  //export logs more than 2000 request
  logsExport(data) {
    return this.http.post<any>(this.url + "logsExport", data);
  }

  //export all report data campaign report
  reportExport(data) {
    return this.http.post<any>(this.url + "reportExport", data);
  }

  //////////////////////////sender id and template approval part//////////////
  //senderidpart
  applySenderID(data) {
    return this.http.post<any>(this.url + "apply_senderId", data);
  }
  getDetSenderID(): Observable<any> {
    return this.http.get(this.url + "get_senderId");
  }
  editSenderID(sender_id, data) {
    return this.http.put<any>(this.url + "edit_senderId/" + sender_id, data);
  }

  //template part
  applyTemplate(data) {
    return this.http.post<any>(this.url + "apply_temps", data);
  }
  getDetTemplate(): Observable<any> {
    return this.http.get(this.url + "get_temps");
  }
  editTemplate(temp_id, data) {
    return this.http.put<any>(this.url + "edit_temps/" + temp_id, data);
  }
  templateImport(data: any) {
    return this.http.post<any>(this.url + "templateImport", data);
  }

  //extra function for multiple use
  csvSampleFile() {
    let data = [
      {
        Name: "ABCd",
        Contact: "898989XXXX",
        Email: "admin@bulk24.com",
        DOB: "01/01/2000",
        CompanyName: "BULK24SMS",
      },
    ];

    var options = {
      fieldSeparator: ",",
      quoteStrings: '"',
      decimalseparator: ".",
      showLabels: false,
      showTitle: false,
      title: "sampleFile.csv",
      useBom: true,
      noDownload: false,
      headers: ["Name", "Contact", "Email", "DOB", "CompanyName"],
    };
    new ngxCsv(data, "SampleFile", options);
  }
}
