import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { Router } from "@angular/router";
import { GlobalConstants } from "src/app/global_constant";
import { ResellerService } from "src/app/shared/reseller/reseller.service";
import { User_List } from "src/app/bulk24/usermanagement/reseller_user.model";
import { ActivatedRoute } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { Dlr_Summary } from "./dlr_summary.model";
import { Location } from "@angular/common";
import { Moment } from "moment/moment.d";
import * as moment from "moment";

export class Trans_log {
  name: any;
  txn_date: any;
  txn_type: any;
  txn_route: any;
  txn_sms: number;
  txn_price: any;
  txn_amount: any;
  txn_user_from: any;
  txn_description: string;
}

@Component({
  selector: "app-reseller",
  templateUrl: "./reseller.component.html",
  styleUrls: ["./reseller.component.scss"],
})
export class ResellerComponent implements OnInit {
  reset_pass_res_user: FormGroup;
  fund_transfer: FormGroup;
  accountForm: FormGroup;
  low_bal_alert: FormGroup;
  account_type: FormGroup;
  url = GlobalConstants.url;
  user_id: any;
  user_details: User_List;
  trans_log: Trans_log;
  dlr_summary: Dlr_Summary[];
  reseller_user_id = null;
  showdlrbox: true;
  err = null;
  msg = null;
  selected: any = "";
  locale: any = {
    format: "DD-MM-YYYY",
    drops: "up",
    opens: "right",
  };
  minDate: any;
  check_demo: boolean;
  //pagination
  p: number = 1;
  count: number = 10;

  //set condition for past date disable
  updateTime(e) {
    console.log(e);

    this.minDate = moment(e);

    //this.maxDate = moment(e).add(15,'days');
  }

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public reseller: ResellerService,
    private route: ActivatedRoute,
    public jwtService: JwtService,
    public location: Location
  ) {
    //get userid which we transerfer fund
    route.paramMap.subscribe((param) => {
      this.reseller_user_id = param.get("id");
    });
  }

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("reseller", this.user_id);

    //reset password
    this.reset_pass_res_user = this.fb.group({
      id: [this.reseller_user_id],
      password: [],
      password_confirmation: [],
    });

    //update reseller users new
    this.accountForm = new FormGroup({
      id: new FormControl("", [Validators.required]),
      name: new FormControl("", [Validators.required]),
      address: new FormControl("", [Validators.required]),
      email: new FormControl("", [Validators.required, Validators.email]),
      contact_number: new FormControl("", [
        Validators.required,
        Validators.minLength(10),
      ]),
      company_name: new FormControl("", [Validators.required]),
      industry: new FormControl("", [Validators.required]),
    });
    //fund Transfer
    this.fund_transfer = this.fb.group({
      txn_user_from: [this.user_id],
      txn_user_to: [this.reseller_user_id],
      txn_route: ["0"],
      txn_type: ["0"],
      txn_sms: [],
      txn_price: [],
      txn_amount: [],
      txn_description: [],
    });
    //low balance alert
    this.low_bal_alert = this.fb.group({});

    // get single  user create by reseller
    this.reseller.getUser(this.reseller_user_id).subscribe(
      (res: any) => {
        console.log(res);
        this.user_details = res[0];
        console.log(this.user_details);
        // if (res[0].expiry !== "") {
        // this.selected = res[0].expiry;
        //   console.log(res[0].expiry);

        //   // this.selected.patchValue=moment(res[0].expiry).format("DD-MM-YYYY");
        // }

        if (res[0].check_demo_user === 1) {
          console.log(res[0].check_demo_user);
          this.account_type.patchValue({
            check_demo_user: true,
          });
        } else {
          this.account_type.patchValue({
            check_demo_user: false,
          });
        }
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );
    //get transaction logs
    this.reseller.transacLog(this.reseller_user_id).subscribe(
      (res: any) => {
        this.trans_log = res;
        console.log(res);
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );
    //get dlr summary
    this.reseller.dlrSummary(this.reseller_user_id).subscribe(
      (res: any) => {
        this.dlr_summary = res;
        console.log("dlr", res);
        if (res == "") {
          this.showdlrbox = true;
        }
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );

    //account type
    // if (this.user_details.check_demo_user == 1) {
    //   this.account_type.patchValue({
    //     check_demo_user: true,
    //   });
    // } else {
    //   this.account_type.patchValue({
    //     check_demo_user: false,
    //   });
    // }
    this.account_type = this.fb.group({
      parent_id: [this.user_id],
      id: [this.reseller_user_id],
      check_demo_user: [],
    });
  }

  //fundTransfer to reseller user
  fundTransfer() {
    console.log(this.fund_transfer.value);
    this.reseller.fundTransfer(this.fund_transfer.value).subscribe(
      (res) => {
        this.ngOnInit(), (this.msg = res);
        console.log(res);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: this.msg.Message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
        // alert(this.err.error);
      }
      // () => {
      //   this.fund_transfer.reset({
      //     parent_id: this.user_id,
      //   });
      // }
    );
  }

  //reset password of reseller user
  resetPassword() {
    console.log(this.reset_pass_res_user.value);
    this.jwtService.updatePassword(this.reset_pass_res_user.value).subscribe(
      (result) => {
        this.ngOnInit(), console.log(result);

        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: result.message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
      },
      () => {
        this.reset_pass_res_user.reset({ id: this.reseller_user_id });
      }
    );
  }
  //update reseller user profile
  updateUser(details: any) {
    console.log(details);
    this.accountForm.patchValue({
      id: details.id,
      name: details.name,
      email: details.email,
      contact_number: details.contact_number,
      address: details.address,
      company_name: details.company_name,
      industry: details.industry,
    });
  }
  updateResUser() {
    console.log(this.accountForm.value);

    this.jwtService.updateProfile(this.accountForm.value).subscribe(
      (res) => {
        this.ngOnInit(), (this.msg = res);
        console.log(res);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: this.msg.Message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
      }
    );
  }
  //changeAccountType
  changeAccountType() {
    console.log(this.account_type.value);
    this.reseller.chnageType(this.account_type.value).subscribe(
      (res) => {
        this.ngOnInit();
        // if (res.Status == true) {
        //   this.account_type.patchValue({
        //     check_demo_user: true,
        //   });
        // } else {
        //   this.account_type.patchValue({
        //     check_demo_user: false,
        //   });
        // }
        console.log(res.Status);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: res.Message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
      }
    );
  }

  //setExpiry

  setExpiry() {
    if (this.selected.startDate !== null) {
      var obj = {};
      obj["date"] = moment(this.selected.startDate._d).format("DD-MM-YYYY");
      obj["id"] = this.reseller_user_id;
      console.log(obj);

      this.reseller.setExpiry(obj).subscribe(
        (res) => {
          this.ngOnInit();
          console.log(res.Status);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: res.Message,
            showConfirmButton: false,
            timer: 1000,
          });
        },
        (error) => {
          this.err = error.error;
        }
      );
    } else {
      alert("First,Select any date");
    }
  }
  removeExpiry() {
    this.selected = "";
    var obj = {};
    obj["date"] = null;
    obj["id"] = this.reseller_user_id;

    console.log(obj);

    this.reseller.setExpiry(this.selected).subscribe(
      (res) => {
        this.ngOnInit();
        console.log(res.Status);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: res.Message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
      }
    );
  }
}
