import { Component, OnInit, Input } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";
import { SmsServiceService } from "src/app/shared/sms-service.service";

import Swal from "sweetalert2/dist/sweetalert2.js";

export class User {
  name: String;
}
export class Info {
  sender_id: String;
  template_id: String;
  message: String;
  group_name: String;
}
export class Dlt {
  sender_id: String;
  template_id: String;
}

@Component({
  selector: "app-sms",
  templateUrl: "./sms.component.html",
  styleUrls: ["./sms.component.scss"],
})
export class SmsComponent implements OnInit {
  user: User;
  info: Info[];
  dlt: Dlt[];
  sendmsgForm: FormGroup;
  msg = null;
  err = null;
  cr: any = null;
  sender_id: 0;
  template_id: 0;
  selectedLang: string = "";
  user_id: any;

  //event handler for the select element's change event
  selectedLangauge(event: any) {
    this.selectedLang = event.target.value;
    console.log(this.selectedLang);
  }

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public smsService: SmsServiceService,
    public jwtService: JwtService,
    public actrouter: ActivatedRoute
  ) {
    this.smsService.info().subscribe((ress: any) => {
      this.info = ress;
      this.dlt = ress.dlt;
    });

    let id = sessionStorage.getItem("id");

    this.sendmsgForm = this.fb.group({
      id: [id],
      // username: [username],
      route: ["A", [Validators.required]],
      sender_id: ["0", [Validators.required]],
      message_type: ["1", [Validators.required]],
      mobile_numbers: [
        "",
        [Validators.required, Validators.pattern("^[0-9]{10}$")],
      ],
      template_id: ["0", [Validators.required]],
      message: ["", [Validators.required]],
      // type: [],
    });
  }

  ///character and credit count
  onKeyUp(boxInput: HTMLTextAreaElement) {
    // console.log(boxInput.value);
    if (this.selectedLang === "2") {
      console.log(this.selectedLang);

      let length = boxInput.value.length;
      let credit = Math.floor(boxInput.value.length / 71) + 1;
      this.cr = credit;
      // console.log(credit);
    } else {
      console.log(this.selectedLang);

      let length = boxInput.value.length;
      let credit = Math.floor(boxInput.value.length / 161) + 1;
      this.cr = credit;
      // console.log(credit);
    }
  }

  get mobile_numbers() {
    return this.sendmsgForm.get("mobile_numbers");
  }

  ngOnInit(): void {
    //get user_id from session
    this.user_id = sessionStorage.getItem("user_id");
    console.log("new header", this.user_id);
  }

  onSubmit() {
    this.smsService.sendsms(this.sendmsgForm.value).subscribe(
      (res) => {
        this.msg = res;
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: this.msg.Status,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
        Swal.fire({
          position: "center",
          icon: "error",
          // title: this.err.Status,
          title: "Field due to improper contain",
          showConfirmButton: false,
          timer: 1000,
        });
      }
      // ,
      // () => {
      //   this.sendmsgForm.reset();
      // }
    );
  }
}
