import { HttpClient } from '@angular/common/http';
import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LoginInAuthService } from 'src/app/login-in-auth.service';
import { JwtService } from 'src/app/shared/jwt.service';

export class User {
  username: String;
}

@Component({
  selector: 'app-otpenter',
  templateUrl: './otpenter.component.html',
  styleUrls: ['./otpenter.component.scss'],
})
export class OtpenterComponent implements OnInit {
  @Input() item = ''; // decorate the property with @Input()
  otpForm: FormGroup;
  err = null;
  msg = null;
  user = null;

  constructor(
    public fb: FormBuilder,
    public jwtService: JwtService,
    private auth: LoginInAuthService,
    public router: Router
  ) {
    const navigation = this.router.getCurrentNavigation().extras;
    console.log(navigation);

    this.otpForm = this.fb.group({
      otpenter: ['', [Validators.required]],
      username: [navigation.state['user'].username],
    });
  }

  ngOnInit(): void {
    if (this.auth.LoggedIn) {
      this.router.navigate(['/user-profile']);
    }
  }
  onSubmit() {
    // var a = this.otpForm.value;
    // console.log(a);
    // return this.router.navigate(['otpsubmit'], {
    //   state: { a },
    // });

    this.jwtService.otpSubmit(this.otpForm.value).subscribe(
      (res) => {
        this.msg = res;
        var a = this.otpForm.value;
        this.router.navigate(['otpsubmit'], {
          state: { a },
        });
      },
      (error) => {
        this.err = error.error.message;
      },
      () => {
        this.otpForm.reset();
      }
    );
  }
}
