import { Component, OnInit } from "@angular/core";
import { PhonebookService } from "src/app/shared/phonebook/phonebook.service";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { FormBuilder, FormGroup } from "@angular/forms";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";
import { JwtService } from "src/app/shared/jwt.service";

export class Data {
  phonebook_name: String;
  phonebook_id: String;
}
export class Ress {}
export class columnList {
  column_id: any;
  column_name: any;
  column_type: any;
}

@Component({
  selector: "app-phonebook",
  templateUrl: "./phonebook.component.html",
  styleUrls: ["./phonebook.component.css"],
})
export class PhonebookComponent implements OnInit {
  create_phonebook: FormGroup;
  edit_phonebook_column_save: FormGroup;
  new_column_save: FormGroup;
  ress: Ress;
  data: Data[];
  // displayVal='';
  message = null;
  msg = null;
  err = null;
  user_id: any;
  url = GlobalConstants.url;

  column_List: columnList[];

  constructor(
    public phonebookService: PhonebookService,
    public fb: FormBuilder,
    private http: HttpClient,
    public jwtService: JwtService
  ) {}

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("phonebook", this.user_id);

    //get phoonebooklist
    this.phonebookService.getPhonebookList().subscribe(
      (ress: any) => {
        console.log(ress);

        this.ress = ress;
        this.data = ress.data;
      },
      (error) => {
        console.log(error);
        if (error.status === 401) {
          Swal.fire({
            position: "top-end",
            icon: "warning",
            title: "Session Expire please login again",
            // title: error.error.message,
            showConfirmButton: false,
            timer: 1000,
          });
          this.jwtService.logout();
        }
      }
    );
    //get_column_list
    this.phonebookService.getColumnList().subscribe(
      (res_column: any) => {
        if (res_column.status == 200) {
          console.log(res_column.data);
          this.column_List = res_column.data;
        } else if (res_column.status == 403) {
          alert(res_column.message);
        }
      },
      (error: any) => {
        if (error.status === 0) {
          alert(error.error.message);
        }
      }
    );

    this.create_phonebook = this.fb.group({
      // id: [this.user_id],
      phonebook_name: [""],
    });
    //forms
    this.new_column_save = this.fb.group({
      // id: [this.user_id],
      column_name: [],
      column_type: ["0"],
      // set_default: ["0"],
    });

    this.edit_phonebook_column_save = this.fb.group({
      column_id: [],
      column_name: [],
      column_type: [],
      //set_default: [],
    });
  }

  //create new phonebook
  onSubmit() {
    this.phonebookService
      .newphonebookSave(this.create_phonebook.value)
      .subscribe(
        (res) => {
          this.ngOnInit(), console.log(res);

          Swal.fire({
            position: "top-right",
            icon: "success",
            title: "Successfully added!",
            showConfirmButton: false,
            timer: 1000,
          });
          console.log(res);
        },
        (error) => {
          this.err = error.error.message;
        }
        // ,
        // () => {
        //   this.create_phonebook.reset({ id: this.user_id });
        // }
      );
  }

  //delete phonebook
  phonebookDelete(phonebook_id: any) {
    console.log(phonebook_id);
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        console.log("phonebook_id", phonebook_id);
        this.http
          .get(this.url + "phonebookDel/" + phonebook_id)
          .subscribe((res) => {
            this.ngOnInit(), console.log(res);
          });
      }
    });
  }

  //add colum in phonebook one by one
  phonebookColumnAdd() {
    console.log(this.new_column_save.value);

    this.phonebookService.column_add(this.new_column_save.value).subscribe(
      (res) => {
        this.ngOnInit(), console.log(this.new_column_save.value);
        this.msg = res;
        Swal.fire({
          position: "top-right",
          title: "Successfully Column added!",
          timer: 500,
        });
        console.log(res);
      },
      (error) => {
        this.err = error.error.message;
      }
      // ,
      // () => {
      //   this.new_column_save.reset({ id: this.user_id });
      // }
    );
  }

  //phonebook Column Delete
  phonebookColumnDel(column_id: any, column_name: any) {
    console.log(column_name);

    if (
      column_name === "Name" ||
      column_name === "Email" ||
      column_name === "Number"
    ) {
      Swal.fire({
        position: "top-end",
        icon: "warning",
        title: " Your can't delete this column",
        showConfirmButton: false,
        timer: 1000,
      });
    } else {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.isConfirmed) {
          console.log("column_id", column_id);
          this.http
            .get(this.url + "phonebookColumnDel/" + column_id)
            .subscribe((res) => {
              console.log("res", res);
              Swal.fire("Deleted!", "Can't delete", "success");
              this.ngOnInit();
            });
          // });
        }
      });
    }
  }

  //phonebook Column Edit
  phonebookColumnEdit(col_list: any) {
    document.getElementById("edit_phonebook_column").style.width = "100%";
    // document.getElementById("main").style.marginLeft = "0px";
    console.log(col_list);

    this.edit_phonebook_column_save.patchValue({
      column_id: col_list.column_id,
      column_name: col_list.column_name,
      column_type: col_list.column_type,
      // column_value: col_list.column_value,
    });

    console.log(this.edit_phonebook_column_save.value);
  }

  phonebookColumnEditSave() {
    console.log(this.edit_phonebook_column_save.value);
    this.phonebookService
      .column_edit(this.edit_phonebook_column_save.value)
      .subscribe(
        (res) => {
          this.ngOnInit();
          console.log(this.edit_phonebook_column_save.value);
          this.msg = res;
          console.log(res);
          // const group_id = this.route.snapshot.paramMap.get("id");
          //   this.phonebookService.getcontactlist(group_id).subscribe((ress: any) => {
          //     this.contactlist = ress;
          //     console.log(ress);
          //   });

          // Swal.fire({
          //   position: "top-left",
          //   icon: "success",
          //   title: "Successfully added!",
          //   showConfirmButton: false,
          //   timer: 1000,
          // });
        },
        (error) => {
          this.err = error.error.message;
        }
      );
  }

  new_contact_pop_closeNav() {
    document.getElementById("edit_phonebook_column").style.width = "0";
    // document.getElementById("main").style.marginLeft = "0";
  }
}
