import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { LoginInAuthService } from 'src/app/login-in-auth.service';
import { JwtService } from 'src/app/shared/jwt.service';

// export class User {
//   username: String;
// }

@Component({
  selector: 'app-otpsubmit',
  templateUrl: './otpsubmit.component.html',
  styleUrls: ['./otpsubmit.component.scss'],
})
export class OtpsubmitComponent implements OnInit {
  msg = null;
  err = null;
  otpuser = null;
  otpSubmitForm: FormGroup;

  constructor(
    public fb: FormBuilder,
    public jwtService: JwtService,
    private auth: LoginInAuthService,
    public router: Router
  ) {
    const navigation = this.router.getCurrentNavigation().extras;
    console.log(navigation);

    this.otpSubmitForm = this.fb.group({
      username: navigation.state['a'].username,
      password: [],
      password_confirmation: [],
    });
  }

  ngOnInit(): void {
    if (this.auth.LoggedIn) {
      this.router.navigate(['/user-profile']);
    }
  }
  onSubmit() {
    // var a = this.otpSubmitForm.value;
    // console.log(a);
    // return this.router.navigate(['signin']);
    this.jwtService.setForgotPassword(this.otpSubmitForm.value).subscribe(
      (res) => {
        this.msg = res;
        var user = this.otpSubmitForm.value;
        console.log(user);

        this.router.navigate(['signin']);
      },
      (error) => {
        this.err = error.error.message;
      },
      () => {
        this.otpSubmitForm.reset();
      }
    );
  }
}
