export class temp_Details {
  content: any;
  sender_id: any;
  submited_at: any;
  template_id: any;
  status: any;
  updated_at: any;
}

@Component({
  selector: "app-templates",
  templateUrl: "./templates.component.html",
  styleUrls: ["./templates.component.css"],
  providers: [MyFilterPipe],
})
export class TemplatesComponent implements OnInit {
  templateForm: FormGroup;
  // senderId: senderID[];
  temp_details: temp_Details[];
  editTempForm: FormGroup;
  msg = null;
  err = null;
  user_id: any;

  url = GlobalConstants.url;
  template_id = null;
  searchedKeyword: string;
  //pagination
  p: number = 1;
  count: number = 15;

  constructor(
    public fb: FormBuilder,
    public smsService: SmsServiceService,
    private http: HttpClient,
    public jwtService: JwtService,
    public location: Location
  ) {}

  ngOnInit(): void {
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("templates", this.user_id);

    // this.smsService.info(this.user_id).subscribe(
    //   (ress: any) => {
    //     this.senderId = ress.sender_id;
    //     // console.log(ress);
    //   },
    //   (error) => {
    //     this.jwtService.error(error);
    //   }
    // );
    this.smsService.getDetTemplate().subscribe(
      (ress: any) => {
        this.temp_details = ress;
        console.log(ress);
      },
      (error) => {
        // this.jwtService.error(error);
      }
    );

    //apply form
    this.templateForm = this.fb.group({
      // id: [this.user_id],
      template_id: [],
      content: [],
    });
    //edit form
    this.editTempForm = this.fb.group({
      // id: [this.user_id],
      template_id: [],
      content: [],
    });
  }

  //on Template Apply
  onTemplateApply() {
    console.log(this.templateForm.value);
    this.smsService.applyTemplate(this.templateForm.value).subscribe(
      (res) => {
        // if (Status == 403) {
        //   Swal.fire({
        //     position: "bottom-end",
        //     icon: "success",
        //     title: res.message,
        //     showConfirmButton: false,
        //     timer: 1000,
        //   });
        // }
        console.log(res);
        this.ngOnInit(), (this.msg = res);
        Swal.fire({
          position: "bottom-end",
          icon: "success",
          title: res.message,
          showConfirmButton: false,
          timer: 1000,
        });
      },
      (error) => {
        this.err = error.error;
      },
      () => {
        this.templateForm.reset({
          id: this.user_id,
        });
      }
    );
  }

  //delete template
  templateDelete(template_id: number) {
    console.log(template_id);
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.http
          .get(this.url + "delete_temps/" + template_id)
          .subscribe((res) => {
            this.ngOnInit(), console.log("res", res);
          });
        Swal.fire({
          title: "Deleted!",
          text: "Your template has been deleted.",
          showConfirmButton: false,
          timer: 1000,
        });
      }
    });
  }
  //edit template option
  templateEdit(temp: any, template_id) {
    console.log(temp);
    console.log(temp.id);
    this.template_id = template_id;
    console.log(this.template_id);
    console.log(temp.sender_id);

    this.editTempForm.patchValue({
      template_id: temp.template_id,
      content: temp.content,
    });
  }

  templateEditSubmit() {
    console.log(this.editTempForm.value);
    this.smsService
      .editTemplate(this.template_id, this.editTempForm.value)
      .subscribe(
        (res) => {
          this.ngOnInit(), (this.msg = res);
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: this.msg.Message,
            showConfirmButton: false,
            timer: 1000,
          });
        },
        (error) => {
          this.err = error.error;
          // Swal.fire({
          //   position: "center",
          //   icon: "error",
          //   // title: this.err.Status,
          //   title: "Field due to improper contain",
          //   showConfirmButton: false,
          //   timer: 1000,
          // });
        },
        () => {
          // this.editTempForm.reset({
          //   'id':this.user_id,
          // });
        }
      );
  }
  //sort filter by status in temp
  sortByStatus(e) {
    console.log(e.target.value);
  }
  exportTemplate() {
    let i = 0;
    // let new_list = this.temp_details;
    let finalData = this.temp_details.map(function (obj) {
      return {
        No: (i = i + 1),
        TemplateId: obj.template_id,
        Content: obj.content,
        Status: obj.status,
        SaveDate: obj.submited_at,
        UpdateDate: obj.updated_at,
      };
    });
    // console.log(finalData);
    var options = {
      fieldSeparator: ",",
      quoteStrings: '"',
      decimalseparator: ".",
      showLabels: false,
      showTitle: false,
      // title: 'CSV DATA',
      useBom: true,
      noDownload: false,
      headers: [
        "S.No",
        "TemplateId",
        "Content",
        "Status",
        "SaveDate",
        "UpdateDate",
      ],
      // headers: ["S.No", "Name", "Number", "email"],
    };
    new ngxCsv(finalData, "srts", options);
  }
}
