import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators,
} from "@angular/forms";
import { JwtService } from "./../../shared/jwt.service";
import { LoginInAuthService } from "src/app/login-in-auth.service";
import { Country } from "src/app/components/register/country.model";

@Component({
  selector: "app-register",
  templateUrl: "./register.component.html",
  styleUrls: ["./register.component.scss"],
})
export class RegisterComponent implements OnInit {
  // signupForm: FormGroup;
  countrydata: Country[];
  err = null;

  constructor(
    public fb: FormBuilder,
    public router: Router,
    public jwtService: JwtService,
    private auth: LoginInAuthService
  ) {}

  //////new form for validation
  signupForm = new FormGroup({
    name: new FormControl("", [Validators.required]),
    username: new FormControl("", [Validators.required]),
    email: new FormControl("", [Validators.required, Validators.email]),
    contact_number: new FormControl("", [
      Validators.required,
      Validators.minLength(10),
    ]),
    country_name: new FormControl("+91", [Validators.required]),
    domain: new FormControl(window.location.host),
    password: new FormControl("", [
      Validators.required,
      Validators.minLength(6),
    ]),
    password_confirmation: new FormControl("", [
      Validators.required,
      Validators.minLength(6),
    ]),
  });
  // get myForm() {
  //   return this.signupForm.controls;
  // }

  get name() {
    return this.signupForm.get("name");
  }
  get username() {
    return this.signupForm.get("username");
  }
  get email() {
    return this.signupForm.get("email");
  }
  get contact_number() {
    return this.signupForm.get("contact_number");
  }
  get password() {
    return this.signupForm.get("password");
  }
  get password_confirmation() {
    return this.signupForm.get("password_confirmation");
  }
  ///get username details
  onKeyUp(boxInput: HTMLTextAreaElement) {
    console.log(boxInput.value);
  }

  ngOnInit() {
    //authgaurd
    if (this.auth.LoggedIn) {
      this.router.navigate(["/services"]);
    }
    // console.log(this.countrydata);

    //getcountrydata
    this.countrydata = this.jwtService.getcountrylist();
  }

  onSubmit() {
    console.log(this.signupForm.value);

    this.jwtService.signUp(this.signupForm.value).subscribe(
      (res) => {
        console.log(res);
      },
      (error) => {
        this.err = error.error;
        console.log(error);
      },
      () => {
        this.signupForm.reset();
        this.router.navigate(["signin"]);
      }
    );
  }
}
