import { Component, OnInit } from "@angular/core";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import { Router } from "@angular/router";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { ResellerService } from "src/app/shared/reseller/reseller.service";
import { JwtService } from "src/app/shared/jwt.service";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { whiteLabelDetails } from "src/app/bulk24/whitelabel/whitelabeldetails.model";

@Component({
  selector: "app-whitelabel",
  templateUrl: "./whitelabel.component.html",
  styleUrls: ["./whitelabel.component.css"],
})
export class WhitelabelComponent implements OnInit {
  user_id = null;
  err = null;
  labelDet: whiteLabelDetails[];
  closeResult = "";
  domainId = "";
  add_Domain: FormGroup;
  showDomain: boolean;
  constructor(
    private modalService: NgbModal,
    public fb: FormBuilder,
    public reseller: ResellerService,
    public jwtService: JwtService,
    public router: Router
  ) {}

  ngOnInit(): void {
    this.showDomain = true;
    //get user_id from session
    this.user_id = localStorage.getItem("user_id");
    console.log("new website", this.user_id);

    // get lists of user create by reseller
    this.reseller.getlabel().subscribe(
      (res: any) => {
        if (res.status == 200) {
          // this.showDomain = true;
          this.labelDet = res.data;
        }
        if (res.status == 204) {
          alert(res.message);
        }
        console.log(res);
      },
      (error) => {
        if (error.status === 401 || 404) {
          // Swal.fire({
          //   position: "top-end",
          //   icon: "warning",
          //   title: "Session Expire please login again",
          //   // title: error.error.message,
          //   showConfirmButton: false,
          //   timer: 1000,
          // });
          // this.jwtService.logout();
        }
      }
    );

    //add_Domain form
    this.add_Domain = this.fb.group({
      // user_id: [this.user_id],
      company_name: [],
      domain: [],
    });
  }

  addDomain(content) {
    this.modalService
      .open(content, { ariaLabelledBy: "modal-basic-title" })
      .result.then(
        (result) => {
          this.closeResult = `Closed with: ${result}`;
        },
        (reason) => {
          this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
        }
      );
  }

  onSubmit() {
    console.log(this.add_Domain.value);
    this.reseller.addDom(this.add_Domain.value).subscribe(
      (res) => {
        this.ngOnInit(), console.log(res);
        this.domainId = res.id;
        // Swal.fire({
        //   position: "bottom-end",
        //   icon: "success",
        //   title: this.msg.Status,
        //   showConfirmButton: false,
        //   timer: 1000,
        // });
      },
      (error) => {
        this.err = error.error;
        // Swal.fire({
        //   position: "center",
        //   icon: "error",
        //   // title: this.err.Status,
        //   title: "Username not available",
        //   showConfirmButton: false,
        //   timer: 1000,
        // });
      },
      () => {
        this.add_Domain.reset({ id: this.user_id });
        this.router.navigate(["managewebsite/" + this.domainId]);
      }
    );
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return "";
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return "";
    } else {
      return `with: ${reason}`;
    }
  }
}
